// src/components/auth/LoginModal.tsx
import React, { useState } from 'react';
import { auth } from '../../firebase'; // auth is potentially Auth | null
import { signInWithEmailAndPassword, sendEmailVerification } from 'firebase/auth';

interface LoginModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSwitchToSignUp: () => void;
}

const LoginModal: React.FC<LoginModalProps> = ({ isOpen, onClose, onSwitchToSignUp }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [showResendVerification, setShowResendVerification] = useState(false);

  const handleLogin = async (event: React.FormEvent) => {
    event.preventDefault();
    setError(null);
    setLoading(true);
    setShowResendVerification(false);

    // --- !!! ADD THIS CHECK AGAIN !!! ---
    if (!auth) {
      setError("Firebase authentication is not available. Cannot log in.");
      setLoading(false);
      console.error("Login attempt failed: auth instance is null.");
      return; // Stop execution if auth is null
    }
    // --- End check ---

    try {
      // Now auth is guaranteed non-null here
      const userCredential = await signInWithEmailAndPassword(auth, email, password);

      if (userCredential.user && !userCredential.user.emailVerified) {
         setError("Email not verified. Please check your inbox (and spam folder).");
         setShowResendVerification(true);
         setLoading(false);
         return;
      }

      onClose();
      setEmail('');
      setPassword('');
    } catch (err: any) {
      // ... (error handling) ...
        console.error("Login Error:", err);
        let errorMessage = 'Failed to login. Please check your credentials.';
        if (err.code === 'auth/user-not-found' || err.code === 'auth/wrong-password' || err.code === 'auth/invalid-credential') { errorMessage = 'Invalid email or password.'; }
        else if (err.code === 'auth/invalid-email') { errorMessage = 'Please enter a valid email address.'; }
        else { errorMessage = err.message || errorMessage; }
        setError(errorMessage);
    } finally {
        // Reset loading state only if we are not showing the resend button
        if (!showResendVerification && loading) { // Check loading state before setting false
             setLoading(false);
        }
    }
  };

  // ... (rest of the component, handleResendVerification, JSX, styles) ...
  const handleResendVerification = async () => {
      // Add null check here too for safety
      if (!auth || !auth.currentUser) {
          setError("Cannot resend verification: Not properly logged in.");
          return;
      }
      try {
          setLoading(true);
          setError(null);
          await sendEmailVerification(auth.currentUser);
          setError("Verification email resent. Please check your inbox.");
          setShowResendVerification(false);
      } catch (err: any) {
          console.error("Resend Verification Error:", err);
          setError(`Failed to resend verification email: ${err.message}`);
      } finally {
          setLoading(false);
      }
  };

    if (!isOpen) return null;

  return (
    <div style={styles.overlay}>
      <div style={styles.modal}>
        <button onClick={onClose} style={styles.closeButton}></button>
        <h2>Login</h2>
        <form onSubmit={handleLogin}>
           <div style={styles.inputGroup}> <label htmlFor="email">Email:</label> <input type="email" id="email" value={email} onChange={(e) => setEmail(e.target.value)} required style={styles.input} /> </div>
          <div style={styles.inputGroup}> <label htmlFor="password">Password:</label> <input type="password" id="password" value={password} onChange={(e) => setPassword(e.target.value)} required style={styles.input} /> </div>
          {error && <p style={styles.errorText}>{error}</p>}
           {showResendVerification && ( <button type="button" onClick={handleResendVerification} disabled={loading} style={styles.resendButton}> {loading ? 'Sending...' : 'Resend Verification Email'} </button> )}
          <button type="submit" disabled={loading || showResendVerification} style={styles.submitButton}> {loading ? 'Logging in...' : 'Login'} </button>
        </form>
        <p style={styles.switchText}> Don't have an account?{' '} <button onClick={onSwitchToSignUp} style={styles.switchButton}> Create Account </button> </p>
      </div>
    </div>
  );
};

// ... styles ...
const styles: { [key: string]: React.CSSProperties } = {
    overlay: { position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, backgroundColor: 'rgba(0, 0, 0, 0.6)', display: 'flex', alignItems: 'center', justifyContent: 'center', zIndex: 1000, },
    modal: { backgroundColor: 'white', padding: '25px 35px', borderRadius: '8px', boxShadow: '0 5px 15px rgba(0,0,0,0.2)', position: 'relative', minWidth: '300px', maxWidth: '90%', },
    closeButton: { position: 'absolute', top: '10px', right: '10px', background: 'none', border: 'none', fontSize: '24px', cursor: 'pointer', color: '#aaa', },
    inputGroup: { marginBottom: '15px', },
    input: { width: '100%', padding: '8px', border: '1px solid #ccc', borderRadius: '4px', boxSizing: 'border-box', marginTop: '5px', },
    errorText: { color: 'red', fontSize: '13px', marginBottom: '10px', textAlign: 'center' },
    submitButton: { backgroundColor: '#007bff', color: 'white', padding: '10px 15px', border: 'none', borderRadius: '4px', cursor: 'pointer', width: '100%', fontSize: '16px', },
    switchText: { marginTop: '20px', textAlign: 'center', fontSize: '14px', color: '#666', },
    switchButton: { background: 'none', border: 'none', color: '#007bff', textDecoration: 'underline', cursor: 'pointer', padding: 0, fontSize: '14px', },
    resendButton: { backgroundColor: '#ffc107', color: '#333', padding: '8px 12px', border: 'none', borderRadius: '4px', cursor: 'pointer', width: '100%', fontSize: '14px', marginTop: '10px', marginBottom: '10px', }
};

export default LoginModal;